    justify-content: center;
    align-items: center;
    height: 500px;
    background-image: url("../public/grids.svg");
    background-color: #fefff7;
    background-size: 15px;
}

.full-length-logo {
    max-width: 1200px;
    width: 700px;

}

.welcome-left {
