    justify-content: center;
    padding: 0px 50px;
    /* flex-direction: column;*/
}

.welcome-left h1 {
    font-family: 'Inconsolata', monospace;
    padding-left: 20px;

    color: #20294f;
    /* flex-direction: column;*/
}

#welcome-right {
    display: block;

}