.projectOverview {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: 0 0 50px 0px;
    /* max-height: 900px; */
    font-family: 'Quicksand', sans-serif;
}

.projectGallery {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0 0 50px 0;
    padding: 20px 30px;
}


.projectOverview img {
    width: 50%;
    height: auto;
    max-height: 400px;
    object-fit: cover;
    object-position: center;
    border-radius: 10px;
    /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); */
    transition: all 0.3s ease-in-out;
    margin: 0 20px;
}

.longImage {
    width: 90% !important;
    margin-top: 80px !important;
}

.projectOverview iframe {
    width: 800px;
    height: 400px;
    object-fit: cover;
    object-position: center;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease-in-out;
}

.projectOverview p {
    width: 60%;
    font-weight: 600;
    font-size: medium;

}

.projectOverview h1 {
    padding: 10px;
    text-align: center;
    font-size: 4rem;
    font-weight: 800;
    margin-bottom: 0;
    margin-top: 0px;
    color: #20294f;


}